import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { isAnyOf } from "@reduxjs/toolkit";
import { addRequest, allContactsRequest, deleteRequest } from "servises/ApiRequests";

export const fetchContacts = createAsyncThunk(
    'contacts/getAll',
    async (_, thunkAPI) => {
      try {
        const contacts = await allContactsRequest();
  
        return contacts;
      } catch (error) {
        return thunkAPI.rejectWithValue(error.message);
      }
    }
  );
  
  export const addContact = createAsyncThunk(
    'contacts/add',
    async (newContact, thunkAPI) => {
      try {
        const contact = await addRequest(newContact);
        console.log('contact: ', contact);
  
        return contact; 
      } catch (error) {
        return thunkAPI.rejectWithValue(error.message);
      }
    }
  );
  
  export const deleteContact = createAsyncThunk(
    'contacts/delete',
    async (contactId, thunkAPI) => {
      try {
        const deletedContact = await deleteRequest(contactId);
  
        return deletedContact; 
      } catch (error) {
        return thunkAPI.rejectWithValue(error.message);
      }
    }
  );
  
  const INITIAL_STATE = {
    contacts: null,
    isLoading: false,
    error: null,
    filterTerm: '',
  };
  
  const contactsSlice = createSlice({
    name: 'contacts',
    initialState: INITIAL_STATE,
    reducers: {
      setFilterTerm: (state, action) => {
        state.filterTerm = action.payload;
      },
    },
    extraReducers: builder =>
      builder
        .addCase(fetchContacts.fulfilled, (state, action) => {
          state.isLoading = false;
          state.contacts = action.payload;
        })
        .addCase(addContact.fulfilled, (state, action) => {
          state.isLoading = false;
          if (Array.isArray(state.contacts)) {
            state.contacts.unshift(action.payload);
          } else {
            state.contacts = [action.payload];
          }
        })
        .addCase(deleteContact.fulfilled, (state, action) => {
          state.isLoading = false;
          state.contacts = state.contacts.filter(
            contact => contact.id !== action.payload.id
          );
        })
        .addMatcher(
          isAnyOf(
            fetchContacts.pending,
            addContact.pending,
            deleteContact.pending
          ),
          state => {
            state.isLoading = true;
            state.error = null;
          }
        )
        .addMatcher(
          isAnyOf(
            fetchContacts.rejected,
            addContact.rejected,
            deleteContact.rejected
          ),
          (state, action) => {
            state.isLoading = false;
            state.error = action.payload;
          }
        ),
  });
  
  export const { setFilterTerm } = contactsSlice.actions;
  export const contactsReducer = contactsSlice.reducer;
  